          keywords: ['ÁîüËæ∞ÂÖ´Â≠ó', 'ÂõõÊü±Êé®ÂëΩ', 'ÂÖ´Â≠ó', 'ÈÅãÂã¢', 'ÂàÜÊûê']
        },
        {
          title: 'Âç†ÊòüÂàÜÊûê',
          description: 'Ë•øÊ¥ãÂç†ÊòüË°ì„Å´„Çà„ÇãÊòüÁõ§ÂàÜÊûê',
          path: '/',
          icon: '‚≠ê',
          keywords: ['Âç†Êòü', 'ÊòüÁõ§', 'ÊòüÂ∫ß', '„Éõ„É≠„Çπ„Ç≥„Éº„Éó', 'Ë•øÊ¥ãÂç†ÊòüË°ì']
        },
