    // Ê∑ªÂä†ÊòüÊòüÂä®Áîª
    this.addStarAnimation();
  },
  
  methods: {
    getStarStyle() {
      // ÈöèÊú∫ÂàÜÂ∏ÉÊòüÊòü
      return {
        position: 'absolute',
        left: Math.random() * 100 + '%',
        top: Math.random() * 100 + '%',
        fontSize: (Math.random() * 0.5 + 0.5) + 'rem',
        animationDelay: Math.random() * 3 + 's',
        opacity: Math.random() * 0.7 + 0.3
      };
    },
    
    goHome() {
      this.$router.push('/');
    },
    
    startAnalysis() {
      this.$router.push('/');
      // ÊªöÂä®Âà∞Ë°®ÂçïÂå∫Âüü
      this.$nextTick(() => {
        const formElement = document.querySelector('.form-container');
        if (formElement) {
          formElement.scrollIntoView({ behavior: 'smooth' });
        }
      });
    },
    
    searchSite() {
      this.showSearch = !this.showSearch;
      if (this.showSearch) {
        this.$nextTick(() => {
          const searchInput = this.$el.querySelector('input');
          if (searchInput) {
            searchInput.focus();
          }
        });
      }
    },
    
    performSearch() {
      if (!this.searchQuery.trim()) return;
      
      const query = this.searchQuery.toLowerCase();
      this.searchResults = this.searchableFeatures.filter(feature => {
        return feature.title.toLowerCase().includes(query) ||
               feature.description.toLowerCase().includes(query) ||
               feature.keywords.some(keyword => keyword.toLowerCase().includes(query));
      });
    },
    
    navigateTo(path) {
      this.$router.push(path);
    },
    
    checkUserData() {
      if (this.hasUserData) {
        this.$router.push('/transit-analysis');
