        type: '404 Error',
        url: window.location.href,
        referrer: document.referrer,
        userAgent: navigator.userAgent,
        timestamp: new Date().toISOString()
      };
      
      console.log('Issue Report:', issueData);
      
      // ËøôÈáåÂèØ‰ª•ÂÆûÁé∞ÂÆûÈôÖÁöÑÈóÆÈ¢òÊä•ÂëäÂäüËÉΩ
