    },
    
    addStarAnimation() {
      const stars = this.$el.querySelectorAll('.star');
      stars.forEach((star) => {
        star.style.animation = `twinkle ${2 + Math.random() * 2}s ease-in-out infinite`;
      });
    }
  }
};
